<template>
  <div class="dashboard">
    <h1>Multi Color Picker ðŸ“Œ</h1>
    <color-picker class="color-wheel" v-model="color"></color-picker>
    <p>HEX:</p>
    <input class="color-input" v-model="color" type="text" />

    <div class="threesome">
      <p>RGB:</p>
      <div class="red">{{ rgb[0] }}</div>
      <div class="green">{{ rgb[1] }}</div>
      <div class="blue">{{ rgb[2] }}</div>
    </div>

    <div class="threesome">
      <p>Sander Units:</p>
      <div class="red">{{ parseFloat(converted[0]).toFixed(2) }}</div>
      <div class="green">{{ parseFloat(converted[1]).toFixed(2) }}</div>
      <div class="blue">{{ parseFloat(converted[2]).toFixed(2) }}</div>
    </div>
  </div>
</template>

<script>
import ColorPicker from "vue-color-picker-wheel";
import converter from "color-convert";

export default {
  name: "Dashboard",
  components: {
    ColorPicker,
  },
  data() {
    return {
      color: "#ffffff",
    };
  },
  computed: {
    rgb: function () {
      return converter.hex.rgb(this.color);
    },
    converted: function () {
      return this.rgb.map((color) => color * 0.003921568627451);
    },
  },
};
</script>

<style lang="scss">
.dashboard {
  p,
  div {
    font-size: 26px;
  }

  .color-input {
    text-align: center;
    padding: 5px;
    border: 0;
    font-size: 26px;
  }

  .color-wheel {
    margin: 0 auto;
  }
  .threesome {
    .red {
      color: #c72100;
    }
    .green {
      color: #1bff06;
    }
    .blue {
      color: #18a8ff;
    }
  }
}
</style>